
import { defineComponent } from 'vue'
import {
    IonGrid,
    IonRow,
    IonCol,
    IonButton
} from "@ionic/vue"
export default defineComponent({
    components: { 
        IonGrid, 
        IonRow, 
        IonCol,
        IonButton
    },
    props: {
        value: {
            type: String,
            required: true
        }
    },
    emits: [
        'onIncrement',
        'onDecrement'
    ],
    setup(p, context) {
        const onIncrement = () => context.emit('onIncrement')
        const onDecrement = () => context.emit('onDecrement')
        return {
            onIncrement,
            onDecrement
        }
    }
})
